
import { formatDate } from "@/common/js/util";
import { callPuExctQryToOICS } from "@/api/api";
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import Imgoicshomepagepicture from "@/assets/img/oicshomepagepicture.jpg";
const imgList = [Imgoicshomepagepicture];

export default {
components: {
Swiper,
SwiperItem,
Grid,
GridItem,
ViewBox,
Badge,
AlertModule
},
data() {
return {
imgList: imgList,
menuList: []
};
},
methods: {
getHomeMenuList() {

let params = {
params: {
RuleName: "GetHomeMenuList",
Parm: { TimePoint: formatDate(new Date(), "yyyyMMdd") }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == "00") {
this.menuList = data.RspMsg.result.ListItems.ListItem;
} else {
this.showModule("获取数据失败", data.RspMsg.msg);
}
this.$store.commit("updateLoadingStatus", { isLoading: false });
};
let callBackError = data => {
this.$store.commit("updateLoadingStatus", { isLoading: false });
this.$vux.toast.show({
text: data.message,
width: "20em",
type: "warn"
});
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
}, //弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
});
}, //获取url
getLink(menuid) {
let link = "";
if (menuid == "1100") link = "/routines_track/track_list";
else if (menuid == "1200")
link = "/site_inspections/site_inspection_list";
else if (menuid == "1300") link = "/warning_processing/index";
else if (menuid == "1400") link = "/subsequent_supervision/index";
return link;
}, //获取菜单名字
getMenuName(menuid) {
let menuName = "";
if (menuid == "1100") menuName = "日常工作跟踪";
else if (menuid == "1200") menuName = "现场检查";
else if (menuid == "1300") menuName = "预警处理";
else if (menuid == "1400") menuName = "事后监督";
return menuName;
}
},
mounted() {


},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit("updateLoadingStatus", { isLoading: true });
vm.menuList = [];
vm.getHomeMenuList();
});
},
beforeRouteLeave(to, from, next) {
next();



}
};



